import React from "react";
import { DialogContent, Dialog, CircularProgress, Typography, Divider, DialogActions, Button } from "@material-ui/core";
import { PhoneOutlined, BusinessOutlined, LocationOnOutlined } from "@material-ui/icons";
import API from "../Services/API";

export default class extends React.Component {
    state = {
        data: null,
        error: null
    }
    constructor(props) {
        super(props);
        this.close = this.close.bind(this);
    }
    componentDidMount() {
        API.GetOrder(this.props.match.params.id)
            .then(
                data => this.setState({ data })
            ).catch(error => this.setState({ error }));
    }
    close() {
        this.props.history.goBack();
    }
    render() {
        const { data, error } = this.state;
        if (!data) return (<Dialog open={true} onClose={this.close} >
            <DialogContent style={{ minWidth: 400, height: 400 }}>
                {error
                    ? (<div style={{ textAlign: "center", padding: 20 }}>
                        <Typography variant="h6">Ocurrió un error</Typography>
                        <Typography variant="body2">{error.message}</Typography>
                        <Typography variant="button">{error.status} {error.statusText}</Typography>
                    </div>)
                    : (<div style={{ textAlign: "center", padding: 100 }}>
                        <CircularProgress size={100} />
                        <Typography variant="h6">CARGANDO...</Typography>
                    </div>)}
            </DialogContent>
        </Dialog>);

        return (<Dialog open={true} onClose={this.close}>
            <DialogContent style={{ minWidth: 400, height: 400 }}>
                <Typography style={{ textAlign: "center" }} variant="h5">ORDEN #{data.numero}</Typography>
                <Divider style={{ marginTop: 10 }} />
                <div>
                    <Typography style={{ display: "inline-block" }} variant="body2">CLIENTE: </Typography>
                    <Typography style={{ display: "inline-block" }} variant="body1">{data.nombre}</Typography>
                </div>
                <div>
                    <Typography style={{ display: "inline-block" }} variant="body2">TLF: </Typography>
                    <Typography style={{ display: "inline-block" }} variant="body1">{data.telefono}</Typography>
                </div>
                <div>
                    <Typography style={{ display: "inline-block" }} variant="body2">DIRECCION: </Typography>
                    <Typography style={{ display: "inline-block" }} variant="body1">{data.direccion}</Typography>
                </div>
                <Divider style={{ marginTop: 10 }} />

                <div style={{ width: "100%", display: "flex", flexDirection: "row" }}>
                    <Typography style={{ flex: 1 }} variant="h6">PRODUCTO</Typography>
                    <Typography variant="h6">CANT</Typography>
                </div>

                {data.detalle.map((item, index) => <RowDet {...item} key={index} />)}

                <div style={{ width: "100%", display: "flex", flexDirection: "row", marginTop: 10 }}>
                    <Typography variant="h6" style={{ flex: 1 }} >Filas: {data.detalle.length}</Typography>
                    <Typography variant="h6">{data.detalle.reduce((a, x) => a + x.cantidad, 0)}</Typography>
                </div>

                <Divider style={{ marginTop: 10 }} />
                <Typography>PAGO : {data.FormaPago}</Typography>
                <Typography>NOTAS: {data.nota}</Typography>

                {data.facturaConDatos && <div>
                    <Divider />
                    <Typography variant="h6">DATOS PARA LA FACTURA</Typography>
                    <Typography>{data.Identificacion}: {data.documento}</Typography>
                    <Typography><BusinessOutlined /> {data.razonSocial}</Typography>
                    <Typography><PhoneOutlined /> {data.telefono}</Typography>
                    <Typography><LocationOnOutlined /> {data.direccion}</Typography>
                </div>}

            </DialogContent>
            <DialogActions>
                <Button variant="outlined" color="secondary" onClick={this.close}>SALIR</Button>
            </DialogActions>
        </Dialog>)
    }
}

function RowDet(props) {
    return (<div style={{ width: "100%", display: "flex", flexDirection: "row" }}>
        <div style={{ flex: 1 }}>
            <Typography variant="subtitle2">{props.descripcion}</Typography>
            <Typography variant="body1">{props.nota}</Typography>
        </div>
        <Typography variant="subtitle2">{props.cantidad}</Typography>
    </div>)
}