import React from "react";
import { Table, TableHead, TableRow, TableCell, TableBody, Checkbox, Typography } from "@material-ui/core";
import { Check } from "@material-ui/icons";
import OrderDetails from "./OrderDetails";
import { Route, withRouter } from "react-router-dom";

export default withRouter(class extends React.PureComponent {
    constructor(props) {
        super(props);
        this.handleClick = this.handleClick.bind(this);
        if (props.onMounted) props.onMounted(null, props.estado);
    }
    handleClick(event) {
        event.stopPropagation();

        const { id } = event.currentTarget;
        // agregar un MODAL
        this.props.history.push(this.props.match.path + "/" + id);
    }
    render() {
        const { data, onSelect, estado, now, timeCol } = this.props;
        const rows = data.filter(x => x.estado === estado);
        return (<Table component="div" style={{ display: "flex", flexDirection: "column", height: "calc(100% - 100px)" }}>
            <TableHead component="div" style={{ display: "block" }}>
                <TableRow component="div" style={{ width: "20%" }}>
                    <TableCell component="div" style={{ textAlign: "center" }}>
                        <Check />
                    </TableCell>
                    <TableCell component="div" style={{ width: "20%" }}>
                        <Typography variant="button">#</Typography>
                    </TableCell>
                    <TableCell component="div" style={{ width: "60%" }}>
                        <Typography variant="button">Cliente</Typography>
                    </TableCell>
                    <TableCell component="div" style={{ width: "20%" }}>
                        <Typography variant="button">Hora</Typography>
                    </TableCell>
                </TableRow>
            </TableHead>
            <TableBody component="div" style={{ flex: 1, overflow: "auto", display: "block" }}>
                {rows.map(x => <Row
                    onClick={this.handleClick}
                    onCheck={onSelect}
                    timeCol={timeCol}
                    key={x.id}
                    {...x}
                    now={now}
                />)}
            </TableBody>
            <Route component={OrderDetails} path={`${this.props.match.path}/:id`} />

        </Table>);
    }
});


class Row extends React.PureComponent {
    render() {
        const { props } = this;
        return (<TableRow
            tabIndex={0}
            style={{ width: "100%", cursor: "pointer" }}
            onContextMenu={props.onContextMenu}
            selected={props.selected}
            hover
            id={"" + props.id}
            onClick={props.onClick}
            component={"div"}
        >
            <TableCell component="div" padding="checkbox" style={{ width: "20%" }}>
                {props.timeCol === "deliveredAt"
                    ? <Check />
                    : <Checkbox checked={props.selected || false}
                        value={"" + props.id}
                        onClick={props.onCheck} />
                }
            </TableCell>
            <TableCell style={{ width: "20%" }} component="div" scope="row" >{props.numero}</TableCell>
            <TableCell style={{ width: "60%" }} component="div">{props.nombre}</TableCell>
            <TableCell style={{ width: "20%" }} component="div">
                {new Date(props[props.timeCol]).toLocaleTimeString()}
            </TableCell>
        </TableRow >);
    }
}

function toHHMM(value) {
    const dif = value / 1000;
    var sec_num = parseInt(dif, 10); // don't forget the second param
    var hours = Math.floor(sec_num / 3600);
    var minutes = Math.floor((sec_num - (hours * 3600)) / 60);

    if (hours < 10) { hours = "0" + hours; }
    if (minutes < 10) { minutes = "0" + minutes; }
    return hours + ':' + minutes;
}